header {
    width: 100%;
    grid-area: header;
    padding-top: 1%;
    background-color: var(--color-middle);
}

.header-buttons {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-wrap: wrap;
    gap: 3%;
}

/* .header-buttons> button:nth-child(3) {
     margin-left: 10%;

} */

.dropdown-menu {
    margin-left: 10%;
    display: flex;
    gap: 3%;
}



.header-button {
    border: 1px solid var(--color-darker);
    border-radius: 25% 25% 0 0;
    /* border-bottom-left-radius: 0.75rem;
    border-bottom-right-radius: 0.75rem; */
    background-color: var(--color-darker);
    color: var(--color-light);
}

.header-button-active {
    border: 1px solid var(--color-light);
    border-radius: 25% 25% 0 0;
    /* border-bottom-left-radius: 0.75rem;
    border-bottom-right-radius: 0.75rem; */
    background-color: var(--color-light);
    color: var(--color-darker);
}

.header-button:hover {
    background-color: var(--color-light);
    color: var(--color-darkest);
    border: 1px solid var(--color-darker);
    cursor: pointer;
}

.icon {
    display: none;
}

@media screen and (max-width: 660px){
    .icon {
        display: block;
        float: right;
        color: var(--color-darkest);
        align-self: center;
        font-size: 1.8rem;
        margin-left: 10%;
    }

    .dropdown-menu
    {
        display: none;
        width: 100%;
        clear: both;
    }
   

    .dropdown-menu.active {
        display: flex;
        margin-top: 1%;
        justify-content: flex-start;
    }
}`, "",{"version":3,"sources":["webpack://./src/static/css/header.css"],"names":[],"mappings":";AACA;IACI,WAAW;IACX,iBAAiB;IACjB,eAAe;IACf,qCAAqC;AACzC;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,qBAAqB;IACrB,eAAe;IACf,OAAO;AACX;;AAEA;;;GAGG;;AAEH;IACI,gBAAgB;IAChB,aAAa;IACb,OAAO;AACX;;;;AAIA;IACI,qCAAqC;IACrC,0BAA0B;IAC1B;0CACsC;IACtC,qCAAqC;IACrC,yBAAyB;AAC7B;;AAEA;IACI,oCAAoC;IACpC,0BAA0B;IAC1B;0CACsC;IACtC,oCAAoC;IACpC,0BAA0B;AAC9B;;AAEA;IACI,oCAAoC;IACpC,2BAA2B;IAC3B,qCAAqC;IACrC,eAAe;AACnB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI;QACI,cAAc;QACd,YAAY;QACZ,2BAA2B;QAC3B,kBAAkB;QAClB,iBAAiB;QACjB,gBAAgB;IACpB;;IAEA;;QAEI,aAAa;QACb,WAAW;QACX,WAAW;IACf;;;IAGA;QACI,aAAa;QACb,cAAc;QACd,2BAA2B;IAC/B;AACJ","sourcesContent":["\r\nheader {\r\n    width: 100%;\r\n    grid-area: header;\r\n    padding-top: 1%;\r\n    background-color: var(--color-middle);\r\n}\r\n\r\n.header-buttons {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: flex-end;\r\n    flex-wrap: wrap;\r\n    gap: 3%;\r\n}\r\n\r\n/* .header-buttons> button:nth-child(3) {\r\n     margin-left: 10%;\r\n\r\n} */\r\n\r\n.dropdown-menu {\r\n    margin-left: 10%;\r\n    display: flex;\r\n    gap: 3%;\r\n}\r\n\r\n\r\n\r\n.header-button {\r\n    border: 1px solid var(--color-darker);\r\n    border-radius: 25% 25% 0 0;\r\n    /* border-bottom-left-radius: 0.75rem;\r\n    border-bottom-right-radius: 0.75rem; */\r\n    background-color: var(--color-darker);\r\n    color: var(--color-light);\r\n}\r\n\r\n.header-button-active {\r\n    border: 1px solid var(--color-light);\r\n    border-radius: 25% 25% 0 0;\r\n    /* border-bottom-left-radius: 0.75rem;\r\n    border-bottom-right-radius: 0.75rem; */\r\n    background-color: var(--color-light);\r\n    color: var(--color-darker);\r\n}\r\n\r\n.header-button:hover {\r\n    background-color: var(--color-light);\r\n    color: var(--color-darkest);\r\n    border: 1px solid var(--color-darker);\r\n    cursor: pointer;\r\n}\r\n\r\n.icon {\r\n    display: none;\r\n}\r\n\r\n@medi